<script>
export default {
  methods: {
    goToPage(url) {
      uni.navigateTo({
        url,
        fail: () => {
          uni.showToast({ title: 'È°µÈù¢ÂºÄÂèë‰∏≠', icon: 'none' })
        }
      })
    },
    showDevTip() {
      uni.showToast({
        title: 'Èó®ËØäÂàÜÊûêÂäüËÉΩÂºÄÂèë‰∏≠',
        icon: 'none'
      })
    },
    getTodayRange() {
      const today = new Date()
      const y = today.getFullYear()
      const m = String(today.getMonth() + 1).padStart(2, '0')
      const d = String(today.getDate()).padStart(2, '0')
      const dateStr = `${y}-${m}-${d}`
      return { startDate: dateStr, endDate: dateStr }
    },
    async exportClinicExcel() {
      try {
        const { startDate, endDate } = this.getTodayRange()
        uni.showLoading({ title: 'ÁîüÊàêÊä•Ë°®...', mask: true })
        const res = await this.$api.callFunction('reports', {
          action: 'exportClinicExcel',
          data: {
            startDate,
            endDate,
            location: 'all',
            printUser: (uni.getStorageSync('userInfo') || {}).name || ''
          }
        })
        uni.hideLoading()
        if (res?.success && res.fileID && res.filename) {
          const urlRes = await wx.cloud.getTempFileURL({ fileList: [res.fileID] })
          const fileUrl = urlRes?.fileList?.[0]?.tempFileURL
          if (fileUrl) {
            this.downloadAndSaveLocal(fileUrl, res.filename, 'Excel')
          } else {
            uni.showToast({ title: 'Ëé∑Âèñ‰∏ãËΩΩÈìæÊé•Â§±Ë¥•', icon: 'none' })
          }
        } else {
          uni.showToast({ title: 'ÁîüÊàêÊä•Ë°®Â§±Ë¥•', icon: 'none' })
        }
      } catch (err) {
        uni.hideLoading()
        uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' })
      }
    },
    async exportUsageExcel() {
      try {
        const { startDate, endDate } = this.getTodayRange()
        uni.showLoading({ title: 'ÁîüÊàêÊä•Ë°®...', mask: true })
        const res = await this.$api.callFunction('reports', {
          action: 'exportClinicUsageExcel',
          data: {
            startDate,
            endDate,
            location: 'all',
            printUser: (uni.getStorageSync('userInfo') || {}).name || ''
          }
        })
        uni.hideLoading()
        if (res?.success && res.fileID && res.filename) {
          const urlRes = await wx.cloud.getTempFileURL({ fileList: [res.fileID] })
          const fileUrl = urlRes?.fileList?.[0]?.tempFileURL
          if (fileUrl) {
            this.downloadAndSaveLocal(fileUrl, res.filename, 'Excel')
          } else {
            uni.showToast({ title: 'Ëé∑Âèñ‰∏ãËΩΩÈìæÊé•Â§±Ë¥•', icon: 'none' })
          }
        } else {
          uni.showToast({ title: 'ÁîüÊàêÊä•Ë°®Â§±Ë¥•', icon: 'none' })
        }
      } catch (err) {
        uni.hideLoading()
        uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' })
      }
    },
    async exportStatsExcel() {
      try {
        const { startDate, endDate } = this.getTodayRange()
        uni.showLoading({ title: 'ÁîüÊàêÊä•Ë°®...', mask: true })
        const res = await this.$api.callFunction('reports', {
          action: 'exportClinicStatsExcel',
          data: {
            startDate,
            endDate,
            location: 'all',
            printUser: (uni.getStorageSync('userInfo') || {}).name || ''
          }
        })
        uni.hideLoading()
        if (res?.success && res.fileID && res.filename) {
          const urlRes = await wx.cloud.getTempFileURL({ fileList: [res.fileID] })
          const fileUrl = urlRes?.fileList?.[0]?.tempFileURL
          if (fileUrl) {
            this.downloadAndSaveLocal(fileUrl, res.filename, 'Excel')
          } else {
            uni.showToast({ title: 'Ëé∑Âèñ‰∏ãËΩΩÈìæÊé•Â§±Ë¥•', icon: 'none' })
          }
        } else {
          uni.showToast({ title: 'ÁîüÊàêÊä•Ë°®Â§±Ë¥•', icon: 'none' })
        }
      } catch (err) {
        uni.hideLoading()
        uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' })
      }
    },
    downloadAndSaveLocal(fileUrl, filename) {
      const fs = wx.getFileSystemManager()
      const folder = `${wx.env.USER_DATA_PATH}`
      const savePath = `${folder}/${filename}`
      try {
        fs.mkdirSync(folder, true)
      } catch (e) {}
      uni.downloadFile({
        url: fileUrl,
        success: (res) => {
          if (res.statusCode === 200) {
            fs.saveFile({
              tempFilePath: res.tempFilePath,
              filePath: savePath,
              success: () => {
                const lower = (filename || '').toLowerCase()
                let fileTypeExt = 'xlsx'
                if (lower.endsWith('.pdf')) fileTypeExt = 'pdf'
                wx.openDocument({
                  filePath: savePath,
                  fileType: fileTypeExt,
                  showMenu: true,
                  fail: () => {
                    uni.showModal({
                      title: 'Êñá‰ª∂Â∑≤‰øùÂ≠ò',
                      content: `Êñá‰ª∂Â∑≤‰øùÂ≠òÂà∞ÔºöÂæÆ‰ø°-Êàë-ÊúçÂä°-Â∞èÁ®ãÂ∫è-ÊàëÁöÑÊñá‰ª∂/${filename}`,
                      showCancel: false,
                      confirmText: 'Áü•ÈÅì‰∫Ü'
                    })
                  }
                })
              },
              fail: () => {
                uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' })
              }
            })
          } else {
            uni.showToast({ title: '‰∏ãËΩΩÂ§±Ë¥•', icon: 'none' })
          }
        },
        fail: () => {
          uni.showToast({ title: 'Êñá‰ª∂‰∏ãËΩΩÂ§±Ë¥•', icon: 'none' })
        }
      })
    }
  }
}
</script>

